
import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { DirektoriTrainingAnggotaService, DirektoriTrainingTrainerService, DirektoriTrainingService } from 'src/app/sdk/hr/services';
import { Training, Training_anggota } from 'src/app/sdk/hr/models';
import { NgxSpinner } from 'ngx-spinner/lib/ngx-spinner.enum';

import { NgxSpinnerService } from 'ngx-spinner';
import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { ToolsLogFlowService } from 'src/app/sdk/user/services/tools-log-flow.service';




@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private direktoriTrainingService: DirektoriTrainingService,
        private direktoriTrainingAnggota: DirektoriTrainingAnggotaService,
        private direktoriTrainingTrainerService: DirektoriTrainingTrainerService,
        private toolsLogFlowService: ToolsLogFlowService,
        private routerActive: ActivatedRoute,
        private router: Router,
        private spinner: NgxSpinnerService,
        private standarApprovalService: StandarApprovalService,
    ) { }

    id_training: string = ''
    dataTraining: Training = {}
    dataTrainingAnggota: Training_anggota[] = []
    dataTrainingTrainerInternal: any[] = []
    dataTrainingTrainerExternal: any[] = []
    ngOnInit() {
        this.routerActive.paramMap.subscribe(
            params => {
                if (params.get('id_training')) {
                    this.id_training = params.get('id_training')
                }

                this.getDetailTraining()
                this.getTrainer()
                this.getAnggota()
                this.getLog()

            }
        )


        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getDetailTraining()
                    this.getTrainer()
                    this.getAnggota()
                    this.getLog()
                }
            }
        )

    }

    params: standarFlow = null;
    id_data_flow: string = ''


    // panggie training
    getDetailTraining() {
        this.spinner.show()
        this.direktoriTrainingService.getDirektoriTrainingsId(this.id_training).subscribe(
            data => {
                this.dataTraining = data.data;

                this.params = {
                    id: data.data.id_training,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_training"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()
            }
        )
    }

    //panggie trainer
    getTrainer() {
        this.direktoriTrainingTrainerService.getDirektoriTrainingTrainers(JSON.stringify({ id_training: this.id_training })).subscribe(
            res => {
                this.dataTrainingTrainerInternal = res.data['internal']
                this.dataTrainingTrainerExternal = res.data['external']
            }
        )
    }

    //panggie pegawai training

    getAnggota() {
        this.direktoriTrainingAnggota.getDirektoriTrainingAnggotas(JSON.stringify({ id_training: this.id_training })).subscribe(
            res => {
                this.dataTrainingAnggota = res.data;
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_training })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }


    goToBack() {
        this.router.navigate(['/hr/direktori/development/training/list'])
    }
}

