import { Component, OnInit } from '@angular/core';
import { DirektoriTrainingAnggotaService, DirektoriPegawaiMasterService, DirektoriTrainingService, DirektoriTrainingTrainerService, DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { NzNotificationService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Training, Training_anggota, Training_trainer, Pegawai_master } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(

        private direktoriTrainingAnggotaService: DirektoriTrainingAnggotaService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private notification: NzNotificationService,

        private direktoriTrainingService: DirektoriTrainingService,
        private direktoriTrainingTrainerService: DirektoriTrainingTrainerService,
        private singleUploadService: SingleUploadService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private routerActive: ActivatedRoute

    ) { }

    dataTraining: Training = {}
    dataTrainingAnggota: Training_anggota[] = []
    dataTrainingTrainer: Training_trainer
    new = new Date();
    id_training: string = '';

    listPegawai: Pegawai_master[] = []
    toogleUpload = false;

    ngOnInit() {
        this.id_training = this.routerActive.snapshot.paramMap.get('id')
        this.form()
        this.getData()
        this.getListPegawai();

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'hr_training.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }
    getListPegawai() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(
            data => {
                this.listPegawai = data.data;
            }
        )
    }

    id_pegawai_anggota: string = '';
    tambahAnggota() {
        if (this.id_pegawai_anggota == '') {
            this.notification.blank(
                'Notification Title',
                'This is the content of the notification. This is the content of the notification. This is the content of the notification.'
            );
            return false;
        }

        this.direktoriTrainingAnggotaService.postDirektoriTrainingAnggotas({ id_pegawai: this.id_pegawai_anggota, id_training: this.id_training }).subscribe(
            res => {
                this.getAnggota();
            }
        )
    }

    listAnggota: any[] = []
    getAnggota() {
        let filter = {
            id_training: this.id_training
        }
        this.direktoriTrainingAnggotaService.getDirektoriTrainingAnggotas(JSON.stringify(filter)).subscribe(
            res => {
                this.listAnggota = res.data
            }
        )
    }
    deleteAnggota(id) {
        this.spinner.show()
        this.direktoriTrainingAnggotaService.deleteDirektoriTrainingAnggotasId(id).subscribe(
            data => {
                this.getAnggota()
                this.spinner.hide()
            }
        )
    }

    //----------Anggota-----------------

    //----------Trainer Internal-----------------

    id_pegawai_trainer: string = '';
    tambahTrainerInternal() {
        if (this.id_pegawai_trainer == '') {
            this.notification.blank(
                'Notification Title',
                'This is the content of the notification. This is the content of the notification. This is the content of the notification.'
            );
            return false;
        }

        this.direktoriTrainingTrainerService.postDirektoriTrainingTrainers({ id_pegawai: this.id_pegawai_trainer, id_training: this.id_training }).subscribe(
            res => {
                this.getTrainer();
            }
        )
    }
    nama_trainer_eksternal: string = '';
    tambahTrainerEksternal() {
        this.direktoriTrainingTrainerService.postDirektoriTrainingTrainers({ nama_trainer: this.nama_trainer_eksternal, id_training: this.id_training }).subscribe(
            res => {
                this.getTrainer();
            }
        )
    }

    dataTrainingTrainerInternal: any[] = []
    dataTrainingTrainerExternal: any[] = []
    getTrainer() {
        let filter = {
            id_training: this.id_training
        }
        this.direktoriTrainingTrainerService.getDirektoriTrainingTrainers(JSON.stringify(filter)).subscribe(
            res => {
                this.dataTrainingTrainerInternal = res.data['internal']
                this.dataTrainingTrainerExternal = res.data['external']
            }
        )

    }

    deleteTrainer(id) {
        this.spinner.show()
        this.direktoriTrainingTrainerService.deleteDirektoriTrainingTrainersId(id).subscribe(
            data => {
                this.getTrainer()
                this.spinner.hide()
            }
        )
    }

    getData() {
        this.spinner.show()
        this.direktoriTrainingService.getDirektoriTrainingsId(this.id_training).subscribe(
            data => {
                this.dataForm.setValue(data.data);
                this.getAnggota()
                this.getTrainer()
                this.spinner.hide()

                this.singleUploadService.param.next({
                    for_module: "hr_training",
                    for_table: "erp_hr.training",
                    for_module_id: data.data.id_training
                })
            }
        )
    }


    dataForm: FormGroupTypeSafe<Training>;
    form() {
        this.dataForm = this.fb.group<Training>(
            {
                nama_training: new FormControl('', [Validators.required]),
                keterangan: new FormControl(''),
                tanggal_mulai_training: new FormControl(''),
                tanggal_selesai_training: new FormControl(''),
                waktu_mulai_training: new FormControl(''),
                waktu_selesai_traning: new FormControl(''),
                lokasi: new FormControl(''),
                id_user: new FormControl(''),
                biaya_training: new FormControl(''),
                id_training: new FormControl(''),
                id_dokumen: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                validasi: new FormControl(''),
                id_data_flow: new FormControl(''),
                id_data_flow_detail: new FormControl(''),
                status: new FormControl(''),
                posisi: new FormControl(''),
                nomor: new FormControl('')
            }
        )
    }

    time: Date | null = null;
    timeend: Date | null = null;


    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }


        this.dataTraining = this.dataForm.value;
        this.direktoriTrainingService.putDirektoriTrainingsIdResponse({id:this.id_training, body: this.dataTraining}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )
    }


    goToBack() {
        this.router.navigate(['/hr/direktori/development/training/list'])
    }

}
